.advantages__wrapper {
  width: 320px;
  padding: 0 20px 0;
  margin: 0 auto;
}

.advantages__title {
  margin: 0 0 40px;
}

.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;
  counter-reset: advantages;
}

.advantages__item {
  display: flex;
  margin-bottom: 20px;
}
.advantages__item:last-child {
  margin-bottom: 0;
}
.advantages__item::before {
  content: "";
  display: block;
  flex-shrink: 0;
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-position: center;
  background-repeat: no-repeat;
  margin-right: 20px;
}
.advantages__item--healthy::before {
  background-image: url("../img/leaf.svg");
  background-size: 30px 32px;
}
.advantages__item--powder::before {
  background-image: url("../img/powder.svg");
  background-size: 29px 31px;
}
.advantages__item--meal::before {
  background-image: url("../img/cutlery.svg");
  background-size: 25px 30px;
}
.advantages__item--time::before {
  background-image: url("../img/scale.svg");
  background-size: 32px 33px;
}

.advantages__text {
  font: 400 14px/18px "Arial", sans-serif;
  color: #444444;
  margin: 0;
}

@media (min-width: 768px) {
  .advantages__wrapper {
    width: 708px;
    padding: 0;
  }
  .advantages__list {
    display: grid;
    grid-template-columns: 250px 250px;
    justify-content: space-between;
    grid-row-gap: 135px;
  }
  .advantages__item {
    width: 250px;
    margin-bottom: 0;
    flex-direction: column;
    position: relative;
  }
  .advantages__item::before {
    width: 80px;
    height: 80px;
    margin-right: 0;
    margin-bottom: 30px;
  }
  .advantages__item::after {
    counter-increment: advantages;
    content: counter(advantages);
    display: block;
    position: absolute;
    left: 120px;
    top: -39px;
    z-index: -1;
    color: #f2f2f2;
    font-size: 280px;
    line-height: 280px;
  }
  .advantages__text {
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .advantages__wrapper {
    width: 1220px;
  }
  .advantages__list {
    grid-template-columns: 250px 250px 250px 250px;
  }
}
.button {
  display: inline-block;
  padding: 10px 26px;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  color: #ffffff;
  background-color: #68b738;
  border: none;
  transition: background-color 0.3s ease;
  cursor: pointer;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--secondary {
  background-color: #f2f2f2;
  color: #444444;
}
.button--secondary:hover {
  background-color: #ebebeb;
}
.button--secondary:active {
  color: rgba(68, 68, 68, 0.3);
}

.button-toggle {
  position: relative;
  top: 0;
  right: 0;
  display: block;
  width: 65px;
  height: 65px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.button-toggle::before {
  content: "";
  display: block;
  position: absolute;
  top: 25px;
  left: 22px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.button-toggle--close::after {
  content: "";
  display: block;
  width: 24px;
  height: 2px;
  position: absolute;
  transform: rotate(-45deg);
  background-color: #000000;
}

.button-toggle--close::before {
  transform: rotate(45deg);
  box-shadow: none;
}

.button-toggle--close::before,
.button-toggle--close::after {
  top: 32px;
  right: 19px;
}

@media (min-width: 768px) {
  .button-toggle {
    display: none;
  }
}
